import Axios from "axios";
import { useEffect, useState } from "react";
import ReactApexChart from "react-apexcharts";
import { useParams } from "react-router-dom";
import { useCoinDispatch, useCoinState } from "../Context/CoinContext";

function ChartPage(){
    const [chartData, setChartData] = useState([{}]);
    const [lastDataTime, setLastDataTime] = useState([1,0]);
    
    const coinState = useCoinState();
    const coinDispatch = useCoinDispatch();
    
    const params = useParams().id;

    const market = coinState.markets.find(item => item.market.split('-')[1]===params)

    const options = {
      chart: {
        type: 'candlestick',
        height: 200,
        toolbar:{
            show:false,
            autoSelected:'pan',
            tools:{
                zoom:false
            }
        },
        events:{
            scrolled: async(_, { xaxis }) => {
                if(xaxis.min<lastDataTime[0]){
                    const ts = new Date(lastDataTime[1])
                    let message;
                    if(ts.getUTCHours()<10) message = `https://api.upbit.com/v1/candles/minutes/60?market=KRW-${params}&count=${lastDataTime[0]-xaxis.min}&to=${ts.getUTCFullYear()}-${ts.getUTCMonth()+1}-${ts.getUTCDate()} 0${ts.getUTCHours()}:${ts.getUTCMinutes()}:${ts.getUTCSeconds()}`
                    else message = `https://api.upbit.com/v1/candles/minutes/60?market=KRW-${params}&count=${lastDataTime[0]-xaxis.min}&to=${ts.getUTCFullYear()}-${ts.getUTCMonth()+1}-${ts.getUTCDate()} ${ts.getUTCHours()}:${ts.getUTCMinutes()}:${ts.getUTCSeconds()}`
                    
                    const response = await Axios.get(message);
                    const data = response.data.map((item)=>{
                        return {
                            x: new Date(item.timestamp),
                            y:[
                                item.opening_price,
                                item.high_price,
                                item.low_price,
                                item.trade_price
                            ]
                        }
                    }).reverse();
                    setLastDataTime([xaxis.min<lastDataTime[0]? xaxis.min:lastDataTime[0] ,response.data[response.data.length-1].timestamp])
                    setChartData([{data:data.concat(chartData[0].data)}])
                }
            }
        }
      }
    }

    useEffect(async()=>{
        const response = await Axios.get(`https://api.upbit.com/v1/candles/minutes/60?market=KRW-${params}&count=40`);
        const data = response.data.map((item)=>{
            return {
                x: new Date(item.timestamp),
                y:[
                    item.opening_price,
                    item.high_price,
                    item.low_price,
                    item.trade_price
                ],
                timestamp: item.timestamp
            }
        }).reverse();
        setChartData([{data:data}])
        setLastDataTime([1,data[0].timestamp])
    },[params])

    return(
        <div className="ChartPage">
            <h1>Chart - {params}</h1>
            <h2>{lastDataTime[1]}</h2>
            {market===undefined &&
                <h2>
                    Not Found Coin {params}
                </h2>
            }
            {market &&
            <>
              <ReactApexChart options={options} series={chartData} type="candlestick" width={1000} />
            </>
            }
        </div>
    )
}

export default ChartPage;
